@import "fluid";

@mixin body {
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
}

@mixin button-title {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.48px;
  text-transform: uppercase;

  @media screen and (min-width: $screen-d) {
    font-size: 15px;
    line-height: 1;
    letter-spacing: 0.75px;
  }
}

@mixin footer {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.1429;
}

@mixin nav-link {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.48px;
  text-transform: uppercase;
}

@mixin footer-default($weight: 500) {
  font-weight: $weight;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.24px;
}

@mixin footer-info {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.24px;

  @media screen and (min-width: $screen-d) {
    line-height: 1.67;
  }
}

@mixin hero-phrase {
  font-weight: 300;
  font-size: fluid(15, 40, 360, 1280);
  line-height: 1.25;
  letter-spacing: 0.4px;
  text-align: center;

  @media screen and (min-width: $screen-d) {
    font-size: 40px;
  }
}

@mixin section-title {
  font-weight: 500;
  font-size: 25px;
  line-height: 1;
  letter-spacing: -0.25px;

  @media screen and (min-width: $screen-tl) {
    font-size: 55px;
    letter-spacing: -0.55px;
  }
}

@mixin section-descr {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.5;

  @media screen and (min-width: $screen-tl) {
    font-size: 15px;
    letter-spacing: 0.45px;
  }
}
